

.App{
  height: 100%;
  width:100%;
  background-color: rgb(170, 216, 111);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.Notes{
  margin-left: 10%;
  padding-top: 5%;
  height:100%;
  width:50%;
  float: left;
  position: relative;
}

.Form{
  float:right;
  height:100%;
  width:40%;
  padding-top: 6%;
  position: relative;
}
.Note{
  padding: 1%;
  font-size: 25px;
}
.Circle{
  border-radius: 50%;
  height:40px;
  width:40px;
  padding:center;
  margin-right: 1%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
